.wizard {
  background: var(--bs-secondary-bg);
  background-clip: border-box;
  border: 0 solid var(--bs-tertiary-bg);
  border-radius: .25rem!important;
  box-shadow: 0 .1rem .2rem rgba(0,0,0,.05);
  margin-bottom: 2rem
}

.wizard .tab-pane {
  padding: 1.25rem!important
}

.wizard.sw-theme-arrows .sw-toolbar-bottom,.wizard.sw-theme-default .sw-toolbar-bottom {
  background: var(--bs-secondary-bg);
  border-top: 1px solid var(--bs-tertiary-bg)
}

.wizard.sw-theme-arrows .step-content,.wizard.sw-theme-default .step-content {
  padding: 10px
}

.wrapper {
  align-items: stretch;
  display: flex;
  width: 100%
}

html[data-layout=boxed] .wrapper {
  border-left: 1px solid var(--bs-border-color);
  border-right: 1px solid var(--bs-border-color);
  margin: 0 auto;
  max-width: 1800px
}

@media(min-width: 1440px) {
  html[data-layout=boxed] .wrapper {
      width:90vw
  }
}

